<template>
    <div class="about">
    <h2>我的购物车</h2>
    <table>
      <thead>
        <tr>
          <th>选择</th>
          <th>商品</th>
          <th>数量</th>
          <th>价格</th>
          <th>小计</th>
          <td>操作</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item of cartList" :key="item.productId">
          <td>
            <van-checkbox checked-color="#FF6700" v-model="item.checked"></van-checkbox>
          </td>
          <td>
            <img :src="item.productImage" />
            <span>{{item.productName}}</span>
          </td>
          <td>
            <van-stepper @change="onChange(item)" v-model="item.productNum" integer />
          </td>
          <td>
            <span>{{item.salePrice}}</span>
          </td>
          <td>
              {{(item.productNum)*(item.salePrice)}}
          </td>
          <td>
            <el-button type="danger" @click="handleDelete(item.productId)">删除</el-button>
          </td>
        </tr>
      </tbody>
    </table>
     
    <van-submit-bar :price="sum"   >
      <van-checkbox v-model="checkAll">全选</van-checkbox>
      <el-button type="danger" @click="handleSubmit()">提交订单</el-button>
    </van-submit-bar>
  </div>
</template>

<script>
    export default {
        name:"Carts",
        data(){
            return{
                cartList:[],
                value:1
            }
        },
        mounted(){
            this.initData()
        },
        methods:{
            initData(){
                this.$http("/users/cartList").then(res=>{
                    this.cartList=res.data.result
                })
            },
          async  onChange(item){
              var {productNum,productId,checked}=item;
              await this.$http.post('/users/cartList/edit',{
                  productId,
                  productNum,
                  checked
              })
            },
            handleDelete(productId){
                this.$confirm('此操作将永久删除该文件，是否继续?','提示',{
                    confirmButtonText:'确定',
                    cancelButtonText:'取消',
                    type:'warning'
                }).then(()=>{
                    this.$http.post('/users/cartList/del',{productId}).then(res=>{
                        console.log(res)
                       this.initData()
                    })
                    this.$message({
                        type:'success',
                        message:'删除成功!'
                    });
                }).catch(()=>{})
            },
            handleSubmit() {
              this.$confirm('此操作将提交订单, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                this.$message({
                  type: 'success',
                  message: '提交成功!'
                });
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: '已取消提交'
                });          
              });
          }
        },
        computed:{
            checkAll:{
                get(){
                    return this.cartList.every(item=>item.checked)
                },
                set(val){
                    this.cartList.forEach(item=>(item.checked=val))
                }
            },
            sum(){
                var total=0;
                this.cartList.forEach(item=>{
                    if(item.checked){
                        total=item["productNum"]*item["salePrice"]+total;
                    }
                })
                return total*100;
            }
        }
    }
</script>

<style scoped>
table >>> .van-checkbox {
  margin-left: 20px;
}
table {
  background: #fff;
  border-collapse: collapse;
  width: 100%;
  border: 1px solid #666;
  border-radius: 15px;
  margin: 0 auto;
  /* display: block; */
  text-align: center;
}
table img {
  width: 100px;
}
table th {
  text-align: center;
}
.about>.van-submit-bar{
  padding:0 20px;
  margin-bottom: 40px;
  position: relative;
}
.about>>>.van-checkbox__icon .van-icon {
    border-color:#FF6700 !important;
}
table >>> .van-stepper .van-stepper__input {
  width: 40px !important;
}
thead {
  background: #333;
  color: #fff;
  line-height: 50px;
}
tbody tr {
  line-height: 100px;
}
h2{
  text-align: center;
}
.btn{
  font-size: 30px;
  width: 100px;
  height: 100px;
}
.van-button--danger{
  display: none;
}
.van-submit-bar__text{
  margin-right: 200px;
}
.el-button{
  position:absolute;
  right:50px;
}
</style>